body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
}

.app-container {
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden; /* Prevent scrollbars on the body */
  touch-action: none; /* Disable all touch actions for the container */
}

.sidebar {
  width: 350px;
  background-color: #1e21286e;
  backdrop-filter: blur(10px);
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  gap: 20px;
  flex-shrink: 0;
  overflow-y: auto;
  padding-bottom: 20px;
  box-sizing: border-box;
}

.made-by-text {
  margin: 10px 0 0 0 ;
  text-align: center;
  font-size: 0.8em;
  color: #aaa;
}

.sidebar h2, .sidebar h3 {
  margin-top: 0;
  color: #61dafb;
}

.control-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.control-group label {
  font-weight: bold;
}

.control-group select, .sidebar button {
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #61dafb;
  background-color: #282c34;
  color: white;
  font-size: 1em;
  flex-shrink: 0; /* Prevent button from shrinking */
}

.sidebar button {
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.sidebar button:hover:not(:disabled) {
  background-color: #61dafb;
  color: #1e2128;
}

.sidebar button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.image-preview-area {
  border: 1px solid #444;
  padding: 10px;
  border-radius: 5px;
  background-color: #2f333c;
}

.image-dropzone {
  border: 2px dashed #61dafb;
  padding: 20px;
  text-align: center;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.image-dropzone--highlight {
  background-color: #3a3f47;
}

.image-dropzone--default {
  background-color: #282c34;
}

.preview-image {
  max-width: 100%;
  max-height: 200px; /* Limit preview image height */
  border-radius: 5px;
  object-fit: contain; /* Ensure aspect ratio is maintained */
  margin-top: 10px;
}

.main-content {
  flex-grow: 1;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main-content h1 {
  color: #61dafb;
  text-align: center;
}

.webgl-viewer-container {
  width: 100%;
  flex-grow: 1; /* Allow WebGL viewer to take available space */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #20232a;
  border-radius: 8px;
  min-height: 400px; /* Ensure a minimum height for the viewer */
  position: relative;
  overflow: hidden; /* Ensure content doesn't spill out */
  box-shadow: 3px 3px 10px rgba(0, 0, 0, 0.5);
}

.viewer-controls {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  padding: 10px;
  border-radius: 10px;
  z-index: 5;
}

.viewer-controls button,
.viewer-controls input[type="range"] {
  background-color: #3d8da5;
  color: white;
  border: none;
  padding: 8px 12px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 14px;
  border-radius: 4px;
  cursor: pointer;
}

.viewer-controls button{
  width: 60px;
}

.viewer-controls input[type="range"] {
  width: 120px;
}

.viewer-controls label {
  color: white;
  display: flex;
  align-items: center;
  white-space: nowrap;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 2em;
  z-index: 10;
}

.main-view-canvas {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 5px;
  margin: 0 auto; /* Center horizontally */
}


.hamburger-menu {
  display: none;
  position: fixed;
  top: 28px;
  left: 18px;
  z-index: 9;
  background: #1e2128;
  border: 1px solid #61dafb;
  border-radius: 5px;
  padding: 6px 10px;
  cursor: pointer;
}

.hamburger-menu span {
  display: block;
  width: 18px;
  height: 3px;
  margin: 4px 0;
  background-color: #61dafb;
  transition: 0.4s;
}

.overlay {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 9;
}

@media (max-width: 768px) {
  .app-container {
    flex-direction: column;
  }

  .sidebar {
    position: fixed;
    left: -400px; /* Hidden by default */
    top: 0;
    height: 100%;
    z-index: 10;
    transition: left 0.3s ease-in-out;
    touch-action: pan-y; /* Allow vertical scrolling but prevent horizontal swipe from interfering with browser navigation */
  }

  .sidebar--open {
    left: 0;
  }
  
  .overlay {
      display: block;
  }

  .hamburger-menu {
    display: block;
  }

  .main-content {
    padding-top: 0px;
  }
}

/* Custom Scrollbar Styles */
.sidebar::-webkit-scrollbar {
  width: 8px;
}

.sidebar::-webkit-scrollbar-track {
  background: #282c34;
  border-radius: 10px;
}

.sidebar::-webkit-scrollbar-thumb {
  background: #61dafb;
  border-radius: 10px;
}

.sidebar::-webkit-scrollbar-thumb:hover {
  background: #4fa3d1;
}

/* Original styles - can be removed or adapted if no longer needed */
.App {
  text-align: center;
  display: none; /* Hide old layout */
}

.output {
  margin-top: 20px;
  display: none; /* Hide old layout */
}

.output canvas {
  border: 1px solid #ccc;
  max-width: 100%;
}

.controls {
  margin-top: 20px;
  display: none; /* Hide old layout */
}

.image-dropzone {
  cursor: pointer;
}

.image-dropzone--highlight {
  opacity: 0.9;
}

.output > canvas {
  background-image: linear-gradient(45deg, #ccc 25%, transparent 25%),
    linear-gradient(135deg, #ccc 25%, transparent 25%),
    linear-gradient(45deg, transparent 75%, #ccc 75%),
    linear-gradient(135deg, transparent 75%, #ccc 75%);
  background-size: 25px 25px; /* Must be a square */
  background-position: 0 0, 12.5px 0, 12.5px -12.5px, 0px 12.5px; /* Must be half of one side of the square */
}

.controls {
  justify-content: center;
  padding: 1em;
}

button {
  border-radius: 3px;
  color: rgb(35, 93, 255);
  border: 2px solid rgb(35, 93, 255);
  background-color: #fff;
  padding: 1em 2em;
  transition: all 200ms ease;
  cursor: pointer;
}

button[disabled] {
  color: #ccc;
  border-color: #ccc;
  cursor: default;
}

a {
  color: rgb(35, 93, 255);
  text-decoration: none;
  transition: all 200ms ease;
}

a:hover {
  opacity: 0.75;
}

h1 {
  text-align: center;
}

.main-title {
  color: #61dafb; /* React blue */
  text-shadow: 0 0 10px #000000, 0 0 20px #a8d2dd, 0 0 30px #61dafb; /* Neon glow */
  margin-bottom: 0.5em;
}


